import React from 'react';

class Navigation extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            links: this.props.pages,
        }
    }

    render() {
        return (
            <nav>
                <div className="nav-wrapper">
                    <a href="{ this.props.pages.home.linkTo }" className="brand-logo center">UMEET</a>
                    <ul className="left hide-on-med-and-down">
                        {
                            Object.keys(this.state.links).map(
                                function (link, i) {
                                    return <li key={i}>
                                        <a href="{ Object.keys(this.state.links[i]).linkTo }">{link.linkText}</a>
                                    </li>
                                }
                            )
                        }
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Navigation;